@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

/* ================================== */

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* 
font-family: 'Raleway', sans-serif; 600/700
font-family: 'Roboto', sans-serif; 400/500
*/
.title {
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 1;
  color: $title-color;
  margin-bottom: 20px;
}

.menu {
  margin-right: auto;
  text-transform: uppercase;
  &__list {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 25px;
    }
  }
}
