html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}


body {
  overflow-x: hidden;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: $main-color;
  background-color: $bg-color;
  
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

img {
  max-width: 100%;
  display: block;
}

section, aside {
  background-color: #fff;
  box-shadow: 0px 5px 10px 0px rgba(50, 50, 50, 0.3);
  
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.section-wrapper {
  flex: 1 1 auto;
  margin-right: 30px;
}

.aside-wrapper {
  width: 50%;
}


article {
  padding: 20px 30px;
}

aside {
  padding: 10px 15px;
}

.main {
  display: flex;
}

button,
input,
textarea {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
  transition: color 0.2s, background-color 0.2s;
}

a, button, input, textarea {
  outline: transparent;
  &:focus {
    border: 1px solid #00B2A0;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}


.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
}


.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}